import React from 'react';
import styled, { css, keyframes } from 'styled-components';
import { Link } from 'gatsby';
import { media } from '../../utils/style-helpers';

const Nav = styled('nav')`
  display: flex;
  height: 100%;
  margin-right: 20px;

  ${media.medium`
    margin-right: 50px;
  `};
  ${media.large`
    margin-right: 150px;
  `};
`;
const animateWidth = keyframes`
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
`;

const selectedItemStyles = ({ to, pathname, theme }) =>
  to === pathname &&
  css`
    color: ${theme.colors.p600};
    :after {
      content: ' ';
      animation: ${animateWidth} cubic-bezier(0.715, 0.41, 0.355, 1) 0.8s
        forwards;
      position: absolute;
      bottom: 0px;
      border-bottom: 2px solid ${theme.colors.p600};
    }
  `;

const itemStyles = ({ theme }) => css`
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  color: ${theme.colors.p200};
  margin: 0px 0.5rem;
  text-decoration: none;
  outline-width: 0;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s;
  letter-spacing: 0.075em;
  font-size: 20px;
  font-weight: 300;
  box-shadow: none;
  :hover {
    color: ${theme.colors.p600};
  }
`;

const Item = styled(Link)`
  ${itemStyles} ${selectedItemStyles}
`;

const routes = [
  { value: '/', label: 'Home' },
  { value: '/about/', label: 'About' },
  { value: '/blog/', label: 'Blog' },
];

function Menu({ location }) {
  return (
    <Nav>
      {routes.map(({ value, label }) => (
        <Item key={value} pathname={location.pathname} to={value}>
          {label}
        </Item>
      ))}
    </Nav>
  );
}

export default Menu;
