import React from 'react';
import styled, { css } from 'styled-components';
import { GoOctoface } from 'react-icons/go';
import { media } from '../../../utils/style-helpers';

const containerStyles = css`
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 20px;
  min-width: 250px;

  ${media.medium`
  justify-content: center;
     margin: 0 25px;
  `};
`;

const Container = styled('div')`
  ${containerStyles}
`;

const githubIconStyles = ({ theme }) => css`
  font-size: 60px;
  color: ${theme.colors.p500};
  margin: 0 10px;
`;

const GithubIcon = styled(GoOctoface)`
  ${githubIconStyles}
`;

const titleStyles = ({ theme }) => css`
  font-size: 20px;
  font-weight: 400;
  color: ${theme.colors.p600};
`;

const Title = styled('div')`
  ${titleStyles}
`;

const hrefBaseStyles = ({ theme }) => css`
  color: ${theme.colors.p400};
  box-shadow: inset 0 -1px 0 0 ${theme.colors.linkHighlight};
  font-weight: 300;
  width: fit-content;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  max-width: 190px;
  text-overflow: ellipsis;
`;

const CommitTitle = styled('a')`
  ${hrefBaseStyles}
  font-weight: 400;
`;

const CommitRepo = styled('a')`
  ${hrefBaseStyles}
  font-size: 12px;
`;

const Description = styled('div')`
  display: flex;
  flex-direction: column;
`;

function LatestCommit({ commit, repo }) {
  return (
    <Container>
      <GithubIcon />
      <Description>
        <Title>Latest Commit</Title>
        <CommitTitle target="_blank" href={commit.url}>
          {commit.message}
        </CommitTitle>
        <CommitRepo target="_blank" href={repo.url}>
          {repo.name}
        </CommitRepo>
      </Description>
    </Container>
  );
}

export default LatestCommit;
