/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { StaticQuery, graphql } from 'gatsby';
import styled, { ThemeProvider, css } from 'styled-components';
import Helmet from 'react-helmet';
import Footer from '../Footer';
import Latest from '../Latest';

import Header from '../Header';
import { GlobalStyles } from '../../styles';
import { media } from '../../utils/style-helpers';
import { darkTheme, mainTheme } from '../../styles/themes/';
import * as GithubMappers from '../../mappers/github';

const contentStyles = ({ theme, location }) => css`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 50px;
  max-width: 960px;
  min-height: 52vh;
  background-color: ${theme.colors.p000};
  flex-grow: 1;
  ${media.huge`
    margin: 0 auto;
  `};
`;

const contentHomeStyles = ({ location }) =>
  location.pathname === '/' &&
  css`
    max-width: 100%;
    margin: 0;
  `;

const Content = styled('div')`
  ${contentStyles}${contentHomeStyles}
`;

function Layout({ children, location, theme }) {
  const [isDark, setDark] = useState(false);

  return (
    <ThemeProvider theme={isDark ? darkTheme : mainTheme}>
      <StaticQuery
        query={layoutQuery}
        render={({ allFeedGithubProfile }) => {
          const { commit, repo } = GithubMappers.getLatestCommit(
            allFeedGithubProfile.edges
          );
          return (
            <>
              <Helmet
                meta={[
                  {
                    name: 'theme-color',
                    content: isDark
                      ? darkTheme.colors.p000
                      : mainTheme.colors.p200,
                  },
                ]}
              />
              <GlobalStyles />
              <Header
                location={location}
                onModeSwitch={checked => setDark(checked)}
                isDark={isDark}
              />
              <Content location={location}>{children}</Content>
              <Footer>
                <Latest commit={commit} repo={repo} />
              </Footer>
            </>
          );
        }}
      />
    </ThemeProvider>
  );
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
  location: PropTypes.object.isRequired,
};

export default Layout;

const layoutQuery = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
    allFeedGithubProfile {
      edges {
        node {
          id
          type
          actor {
            display_login
            avatar_url
          }
          repo {
            name
            url
          }
          payload {
            commits {
              url
              message
            }
          }
        }
      }
    }
  }
`;
